body {
  margin: 0%;
}

.container {
  color: #fff;
  background-color: #23262df0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0% 20px 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

.title {
  text-align: center;
  margin-bottom: 20px;
}

.pagination {
  justify-content: center;
  margin-top: 5px;
  display: flex;
}

.pagination-list {
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 1px;
  list-style: none;
  display: flex;
}

.pagination-item.down-arrow {
  padding-top: 11px;
}

.pagination-item {
  color: #fff;
  cursor: pointer;
  background-color: #4b4b4b;
  border: 2px solid #fff;
  border-radius: 12px;
  margin: 3.5px;
  padding: 5px 10px;
  transition: background-color .3s;
}

.pagination-item:hover {
  background-color: #626262;
}

.pagination-item.active {
  color: #333;
  background-color: #fff;
}

.pagination-item.disabled {
  opacity: 0;
  cursor: default;
}

.pagination-item:first-child {
  margin-left: 0;
}

.pagination-item:last-child {
  margin-right: 0;
}

.pagination-top {
  margin-inline-start: auto;
  background-color: #1d1e1f7b;
  border: 3px solid #000000a7;
  border-radius: 15px;
  margin-top: 0;
  padding: 0 8px;
}

.pagination.button-space {
  margin: 0;
  padding: 0;
}

.pagination-item.page-end {
  cursor: not-allowed;
  opacity: .25;
}

a {
  color: #fff;
  font-weight: 550;
  text-decoration: none;
}

.pokemon-list {
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.pokemon-item {
