.poke-img>img{
	height: 250px;
	width:auto;
}
.info-div {
	width: 90%;
	height: auto;
	position: relative;
	top: 50px;
}
.poke-img {
	width: fit-content;
	border-radius: 2rem;
	background: rgb(34, 33, 39);
		background: linear-gradient(161deg, rgba(34, 33, 39, 1) 28%, rgba(110, 110, 118, 1) 100%, rgba(212, 216, 217, 1) 100%);
}
.container {
	justify-content: flex-start;
}

@media screen and (max-width:600px) {
	.poke-img>img {
			height: 150px;
			width: auto;
		}
}
@media screen and (max-width:400px) {
	.poke-img>img {
		height: 100px;
		width: auto;
	}
	.poke-img{
		border-radius: 1rem;
	}
}